<template>
  <div>
    <el-row
      :gutter="10"
      v-loading="javascriptStore.loading"
      element-loading-text="Cargando..."
      type="flex"
      justify="center"
    >
      <el-col :xs="23" :sm="23" :md="12" :lg="12" :xl="12">
        <div v-if="templateStore.isFile === '1'">
          <label>Arrastra tu archivo js para agregarlo al sitio</label><br />

          <!-- Uploader -->
          <el-upload
            name="fileJS"
            action="#"
            class="upload-demo"
            :show-file-list="false"
            drag
            :http-request="uploadFile"
          >
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">
              Suelta tu archivo aquí o <em>haz clic para cargar</em>
            </div>
            <div slot="tip" class="el-upload__tip">
              Solo archivos CSS con un tamaño menor de 10 MB
            </div>
          </el-upload>
          <!-- End Uploader -->
        </div>

        <!-- Add CDN -->
        <div v-if="templateStore.isFile === '2'">
          <label>Agrega url CDN par agregarla al sitio</label><br /><br />
          <el-row :gutter="10">
            <el-col :span="24">
              <el-input placeholder="Url CDN" v-model="cdnUrl"></el-input>
            </el-col>
          </el-row>
          <el-button type="success" @click="uploadCDN"> Agregar CDN </el-button>
        </div>
        <!-- End CDN -->
      </el-col>

      <el-col
        :xs="23"
        :sm="23"
        :md="15"
        :lg="17"
        :xl="15"
        v-if="templateStore.isFile === '1' || templateStore.isFile === '2'"
      >
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>Archivos cargados en el sitio</span>
          </div>
          <div class="text item">
            <div class="custom-tree-container">
              <div class="block">
                <!-- Start Tree -->
                <el-tree
                  :data="javascriptStore.list"
                  node-key="id"
                  :props="defaultProps"
                  default-expand-all
                  @node-drop="handleDrop"
                  draggable
                  :allow-drop="allowDrop"
                >
                  <span class="custom-tree-node" slot-scope="{ node, data }">
                    <span>{{ node.label }}</span>
                    <span>
                      <el-button
                        type="text"
                        size="mini"
                        @click="() => deleteFile(node, data)"
                      >
                        Eliminar
                      </el-button>
                    </span>
                  </span> </el-tree
                ><br />
                <!-- End Tree -->
                <div align="right">
                  <el-button
                    type="primary"
                    size="medium"
                    aling="right"
                    @click="updateTree()"
                  >
                    Actualizar
                  </el-button>
                </div>
              </div>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { useStoreJavaScript } from '../../../../stores/template/files/javascriptStore'
import { upload } from '@/api/utilities/files/javascript'
import { useStoreTemplate } from '../../../../stores/template/templateStore'
import _ from 'lodash'

// import { codemirror } from 'vue-codemirror'

const javascriptStore = useStoreJavaScript()
const templateStore = useStoreTemplate()

export default {
  name: 'Javascript',
  components: {
    // codemirror
  },
  data () {
    return {
      loading: false,
      cdnUrl: '',
      javascriptStore,
      templateStore,
      defaultProps: {
        label: 'name'
      },
      cmOptions: {
        tabSize: 4,
        mode: 'javascript',
        theme: 'dracula',
        lineNumbers: true,
        line: true
      }
    }
  },
  created () {
    // javascriptStore.fetchJavascript()
    // javascriptStore.fetchCustomJavascript()
  },
  computed: {
    codemirror () {
      return this.$refs.editorJavascript.codemirror
    }
  },
  mounted () {},
  methods: {
    async uploadCDN () {
      templateStore.loading = true
      javascriptStore.file.name = this.cdnUrl
      javascriptStore.file.pathFile = this.cdnUrl
      javascriptStore.file.type = 'External'
      javascriptStore.file.order = javascriptStore.list.length + 1
      const keyName = _.cloneDeep(templateStore.keyName)
      const fileData = _.cloneDeep(javascriptStore.file)

      javascriptStore.storeJavascriptFile(fileData, keyName).finally(() => {
        this.cdnUrl = ''
        templateStore.loading = false
      })
    },
    async uploadFile (param) {
      templateStore.loading = true
      const formData = new FormData()
      formData.append('file', param.file)
      formData.append('fileName', param.file.name)
      formData.append('folder', 'template')
      upload(formData)
        .then((response) => {
          javascriptStore.file.name = param.file.name
          javascriptStore.file.pathFile = response.data
          javascriptStore.file.type = 'Internal'
          javascriptStore.file.order = javascriptStore.list.length + 1

          const fileData = _.cloneDeep(javascriptStore.file)
          const keyName = _.cloneDeep(templateStore.keyName)

          javascriptStore.storeJavascriptFile(fileData, keyName)
        })
        .catch((response) => {
          // console.log(response)
          param.onError()
        })
        .finally(() => (templateStore.loading = false))
    },
    handleDrop () {
      javascriptStore.orderFilesJavascript()
    },
    allowDrop (draggingNode, dropNode, type) {
      if (type !== 'inner') {
        return true
      }
    },
    updateTree () {
      const keyName = _.cloneDeep(templateStore.keyName)
      const listData = _.cloneDeep(javascriptStore.list)

      javascriptStore.updateJavascriptFiles(listData, keyName)
    },
    deleteFile (node, data) {
      this.$confirm(
        'Se borrara el arvhivo de su sitio, ¿Esta seguro?',
        'Cuidado',
        {
          confirmButtonText: 'OK',
          cancelButtonText: 'Cancelar',
          type: 'warning'
        }
      )
        .then(() => {
          const parent = node.parent
          const children = parent.data.children || parent.data
          const index = children.findIndex((d) => d._id === data._id)
          const keyName = _.cloneDeep(templateStore.keyName)
          const id = _.cloneDeep(data._id)
          javascriptStore.deleteJavascriptFile(keyName, id).then((response) => {
            children.splice(index, 1)
          })
        })
        .catch(() => {
          this.$message({
            showClose: true,
            type: 'info',
            message: 'Se cancelo la operación'
          })
        })
    },
    saveCustomJavascript () {
      const keyName = _.cloneDeep(templateStore.keyName)
      const customData = _.cloneDeep(javascriptStore.content)

      javascriptStore.updateCustomJavascript(keyName, customData)
    }
  }
}
</script>

<style></style>
