<template>
  <div class="iframe-container">
    <!-- NavBar -->
    <el-row>
      <el-card class="box-card" v-if="templateStore.isVisibleNavBar">
        <el-menu
          :default-active="activeIndex"
          class="el-menu-demo"
          mode="horizontal"
          @select="handleSelect"
        >
          <el-button
            type="success"
            size="small"
            class="button-nav-bar"
            plain
            @click="saveTemplate"
            >Guardar plantilla</el-button
          >

          <el-button
            type="primary"
            size="small"
            class="button-nav-bar"
            plain
            @click="clearTemplate"
            >Limpiar Plantilla</el-button
          >

          <el-button
            type="danger"
            size="small"
            class="button-nav-bar"
            plain
            @click="goToIndex"
            >Cancelar</el-button
          >

          <el-button
            type="warning"
            size="small"
            class="button-nav-bar"
            plain
            @click="showEditorSettings"
            >Configuración de Editor</el-button
          >

          <el-submenu index="1" class="files-nav-bar">
            <template slot="title">Archivos CSS</template>
            <el-menu-item index="1-1">Añadir un archivo CSS</el-menu-item>
            <el-menu-item index="1-2">Añadir un CDN de CSS</el-menu-item>
            <!-- <el-menu-item index="1-3">Editar CSS personalizado</el-menu-item> -->
          </el-submenu>

          <el-submenu index="2" class="files-nav-bar">
            <template slot="title">Archivos JavaScript</template>
            <el-menu-item index="2-1"
              >Añadir un archivo JavaScript</el-menu-item
            >
            <el-menu-item index="2-2">Añadir un CDN de JavaScript</el-menu-item>
            <!-- <el-menu-item index="2-3"
              >Editar JavaScript personalizado</el-menu-item
            > -->
          </el-submenu>
        </el-menu>
      </el-card>
    </el-row>
    <!-- End NavBar -->

    <!-- Close and Open Bar -->
    <el-tooltip
      class="item"
      effect="dark"
      :content="templateStore.isVisibleNavBar ? 'Cerrar Barra' : 'Abrir Barra'"
      placement="right"
    >
      <el-button type="text" class="test" @click="templateStore.showNavBar">
        <i
          class="el-icon-top icon-button"
          v-if="templateStore.isVisibleNavBar"
        ></i>
        <i class="el-icon-bottom icon-button" v-else></i>
      </el-button>
    </el-tooltip>
    <!-- End Close and Open Bar -->

    <!-- Dialog -->
    <el-dialog
      :title="templateStore.isJavascript ? 'Añade JavaScript' : 'Añande CSS'"
      :visible.sync="templateStore.dialogVisible"
      width="50%"
      :before-close="templateStore.handleClose"
    >
      <javascript v-if="templateStore.isJavascript" />

      <css v-else />

      <!-- Custom CSS -->
      <el-row
        :gutter="10"
        v-loading="templateStore.loading"
        type="flex"
        justify="center"
        v-if="templateStore.isFile === '3'"
      >
        <el-col :xs="23" :sm="23" :md="23" :lg="23" :xl="23">
          <h2>
            Edita el
            {{ templateStore.isJavascript ? 'JavaScript' : 'CSS' }}
            Personalizado
          </h2>
          <div>
            <codemirror :v-model="content" :options="cmOptions" />
          </div>
        </el-col>
      </el-row>
      <!-- End Custom CSS -->

      <span slot="footer" class="dialog-footer">
        <el-button @click="templateStore.dialogVisible = false"
          >Cancel</el-button
        >
        <el-button
          type="success"
          @click="
            templateStore.isFile !== '3'
              ? useAssets()
              : onSaveCustomContent()
          "
          >Guardar</el-button
        >
      </span>
    </el-dialog>
    <!-- End Dialog -->

    <el-row
      v-loading="templateStore.loading"
      element-loading-text="Cargando..."
      :gutter="10"
    >
      <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
        <!-- Start Iframe -->
        <el-card class="box-card">
          <div class="text item">
            <iframe id="viewEditor" style="height: 650px; width: 100%"></iframe>
          </div>
        </el-card>
        <!-- End Iframe -->
      </el-col>
    </el-row>
    <!-- End Menu -->
  </div>
</template>
<script>
// import { mapState } from 'vuex'
import { mapGetters } from 'vuex'
import { update, getMain } from '@/api/template/main'
import { useStoreConfig } from '../../../stores/configStore'
import { useStoreTemplate } from '../../../stores/template/templateStore'
import { useStoreCss } from '../../../stores/template/files/cssStore'
import { useStoreJavaScript } from '../../../stores/template/files/javascriptStore'

import javascript from '../components/content/javascript.vue'
import css from '../components/content/css.vue'
import { codemirror } from 'vue-codemirror'
import _ from 'lodash'

const configStore = useStoreConfig()
const templateStore = useStoreTemplate()
const cssStore = useStoreCss()
const javascriptStore = useStoreJavaScript()

var viewEditor

export default {
  components: {
    javascript,
    codemirror,
    css
  },
  data () {
    return {
      configStore,
      templateStore,
      cssStore,
      javascriptStore,
      isFile: true,
      activeIndex: '1',
      content: 'ds',
      contentHtml: '',
      cmOptions: {
        tabSize: 4,
        mode: templateStore.isJavascript ? 'javascript' : 'text/css',
        theme: 'dracula',
        lineNumbers: true,
        line: true
      }
    }
  },
  computed: {
    ...mapGetters(['token'])
  },
  mounted () {
    this.$nextTick(async function () {
      viewEditor = document.getElementById('viewEditor')
      this.useAssets()
    })

    window.addEventListener('message', (event) => {
      this.contentHtml = event.data
    })
  },
  async created () {
    templateStore.isEdit = true
    templateStore.keyName = this.$route.params.keyName

    const keyName = _.cloneDeep(templateStore.keyName)

    await cssStore.fetchCss(keyName)
    await cssStore.fetchCustomCss(templateStore.keyName)

    await javascriptStore.fetchJavascript(keyName)
    await javascriptStore.fetchCustomJavascript(templateStore.keyName)

    await templateStore.showTemplate(keyName)
  },
  methods: {
    handleGetMain () {
      this.loading = true
      getMain(templateStore.keyName)
        .then((response) => {
          this.form = response.data
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },
    async onUpdate () {
      await update(this.form, templateStore.keyName)
        .then((response) => {
          this.$message({
            showClose: true,
            message: '¡Actuliazacion completa!',
            type: 'success'
          })
          this.goListMain()
        })
        .catch(this.responseCatch)
    },
    goToIndex () {
      this.$router.push({ name: 'main.index' })
    },
    useAssets () {
      templateStore.dialogVisible = false
      viewEditor.setAttribute(
        'src',
        configStore.baseUrl + 'manager/editor/template/' + templateStore.keyName
      )
    },
    async getInfoIframe (mode) {
      return new Promise((resolve, reject) => {
        var frameContent = document.getElementById('viewEditor').contentWindow
        frameContent.postMessage(mode, '*')

        setTimeout(
          () =>
            (this.contentHtml !== undefined) || (this.contentHtml !== '')
              ? resolve(this.contentHtml)
              : reject(new Error('esperando resultado')),
          3000
        )
      })
    },
    async saveTemplate () {
      await this.getInfoIframe('html#' + this.token)

      if (this.contentHtml === undefined || this.contentHtml === '') {
        this.$message.error('Error al guardar. Intenta de nuevo.')
      } else {
        templateStore.loading = true

        const dataEditor = _.cloneDeep(this.contentHtml)
        const keyName = _.cloneDeep(templateStore.keyName)

        templateStore
          .updateContentHtml(dataEditor, keyName)
          .then(() => {
            this.goToIndex()
          })
          .finally(() => (templateStore.loading = false))
      }
    },
    async clearTemplate () {
      templateStore.loading = true
      await this.getInfoIframe('clear')
      const dataEditor = _.cloneDeep(this.contentHtml)
      const keyName = _.cloneDeep(templateStore.keyName)

      templateStore.updateContentHtml(dataEditor, keyName).finally(() => {
        templateStore.loading = false
        this.useAssets()
      })
    },
    async showEditorSettings () {
      await this.getInfoIframe('settings')
    },
    onSaveCustomContent () {
      if (templateStore.isJavascript) {
        const customData = _.cloneDeep(javascriptStore.content)
        const keyName = _.cloneDeep(templateStore.keyName)
        javascriptStore.updateCustomJavascript(customData, keyName)
      } else {
        // const customData = _.cloneDeep(cssStore.content)
        const keyName = _.cloneDeep(templateStore.keyName)
        console.log('~ keyName', keyName)
        // cssStore.updateCustomCss(customData, keyName)
      }
    },
    handleSelect (key, keyPath) {
      templateStore.dialogVisible = true

      if (keyPath[0] === '2') {
        templateStore.isJavascript = true
        templateStore.isFile = key.split('-')[1]
      } else {
        templateStore.isJavascript = false
        templateStore.isFile = key.split('-')[1]
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.iframe-container {
  padding: 5px;
}
.button-group {
  padding-left: 5px;
}
.button-end {
  float: right;
  padding: 5px;
}
.pr-20 {
  padding-right: 20px;
}
.pt-0 {
  padding-top: 0;
}
.mt-10-n {
  margin-top: -10px;
}
.files-nav-bar {
  margin-right: 3rem;
  float: right;
  margin-top: -10px;
}
.button-nav-bar {
  float: right;
  margin-right: 3rem;
}
.test {
  width: 30px;
  height: 30px;
  box-shadow: 0px 0px 5px 0.5px rgb(63, 62, 62);
  margin-top: -1rem;
  display: flex;
  flex-wrap: wrap;
}
.icon-button {
  margin-left: 0.4rem;
  margin-top: -20px;
}
.label-dialog {
  margin-top: -200px;
}
</style>
