import request from '@/utils/request'

export function search (data) {
  return request({
    url: '/template/main/search',
    method: 'post',
    data
  })
}

export function getPage (page, noitems) {
  return request({
    url: '/template/main/search?page=' + page + '&item=' + noitems,
    method: 'post'
  })
}

export function getMain (idMain) {
  return request({
    url: '/template/main/' + idMain,
    method: 'get'
  })
}
export function destroy (idMain) {
  return request({
    url: '/template/main/' + idMain,
    method: 'delete'
  })
}
export function store (data) {
  return request({
    url: '/template/main',
    method: 'post',
    data
  })
}

export function update (data, idMain) {
  return request({
    url: '/template/main/' + idMain,
    method: 'put',
    data
  })
}
export function listTitle () {
  return request({
    url: '/template/main/title',
    method: 'get'
  })
}
